body {
  background-color: #bcd400;
}
button{
  width: 160px;
  height: 50px;
  background-color: transparent;
  border: 2px solid #0052d4;
  color: #0052d4;
  margin-top: 30px;
  font-size: 30px;
  margin-bottom: 30px;
  cursor: pointer;
}
a{
  text-decoration: none;
  color: #0052d4;
  font-weight: bold;
}
.App {
  text-align: center;
  margin: auto;
  background-color: #bcd400;
  color: #0052d4;
}
.container {
  width: 65vmin;
  height: 65vmin;
  margin: auto;
  background-color:  #0052d4;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-content: space-evenly;
}
  
.square {
  background-color: #bcd400;
  height: 20vmin;
  width: 20vmin;
  box-sizing: border-box;
  font-size: 40px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
